import React from "react";
import { NavLink } from "react-router-dom";

const Dashboard = () => {
    return (
        <div className="w-1/4 flex justify-between">
            <NavLink
                className="group m-1 relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                to="/admin"
            >
                Admin
            </NavLink>
            <NavLink
                className="m-1 group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                to="/agent"
            >
                Agent
            </NavLink>
        </div>
    );
};

export default Dashboard;
