import React, { memo, useEffect, useRef } from "react";
import "amazon-connect-streams";

const Ccp = () => {
    const ref = useRef();

    useEffect(() => {
        try {
            console.log("init start");
            if (typeof window === "undefined")
                throw new Error("window missing");
            if (typeof window.connect === "undefined")
                throw new Error("global connect missing");
            window.connect.core.initCCP(ref.current, {
                ccpUrl: `https://techsee-test.my.connect.aws/connect/ccp-v2?enablejsapi=1`,
                loginPopup: true,
                loginPopupAutoClose: true,
                pageOptions: {
                    enableAudioDeviceSettings: true,
                    enablePhoneTypeSettings: true,
                },
                softphone: { allowFramedSoftphone: true },

            });
            console.log("init end");
        } catch (e) {
            console.error(e);
        }
    }, [ref]);
    console.log(ref)
    return (
        <div
            ref={ref}
            style={{
                width: "100%",
                height:"500px"
            }}
        />
    );
};

export default memo(Ccp);
